@import url('https://fonts.googleapis.com/css2?family=Londrina+Solid&display=swap');


.main-area h1{
    font: normal 600 4rem 'Londrina Solid', cursive;
    margin-bottom: 6rem;
    margin-top: 2rem;
    text-shadow: 0px 1px 1px #000;
}

.main-area{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.main-area .avatar-card{
    height: 35rem;
    width: 30rem;
    background-color: #ffffff;
    box-shadow: 3px 3px 5px 6px #ccc;
    display: flex;
    align-items: center;
    justify-content: baseline;
}

.main-area .avatar-card img{
    height: 100%;
    width: 100%;
    margin: auto;
}

.main-area  button{
    height: 5rem;
    width: 20rem;
    border: none;
    color: whitesmoke;
    font: normal 600 2rem 'Londrina Solid', cursive;
    background-color: blue;
}
.main-area  button:hover{
    color: blue;
    background-color:white;
    transition: all 0.3s ease;
    border: 2px solid blue;
}

.main-area input{
    height: 6rem;
    margin: 4rem;
    padding: 0rem 1rem;
    background: #d8e6d9;
    color: #000;
    flex-direction: column;
    width: 28rem;
    font: normal 600 3rem 'Londrina Solid', cursive;
}

input:focus {
    outline:none;
}
.main-area input::placeholder{
    color: #000;
    font: normal 600 3rem 'Londrina Solid', cursive;
    background: #d8e6d9;
}